	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Not That Starbuck","date":"15 Jul 2005","slug":"/2005-07-15-not-that-starbuck/","tags":"Y2005"},"html":"<p>We'll be flying to Minnesota next week and spending two weeks on Lake Minnewaska at my parents' lake house near <a href=\"http://www.starbuckmn.org/\" title=\"Starbuck Minnesota USA - City and Chamber of Commerce\">Starbuck</a> (not to be confused with the <a href=\"http://www.starbucks.com/\">coffee shop chain</a>). I just realized that they no longer have a telephone at the lake, since they have a cell phone. And I doubt that Starbuck (without the final s) has WiFi. I guess that means that posting to PapaScott will be fairly light while we are there. </p>\n<p>On the other hand, we won't be getting any pesky emails from our respective employers.</p>"},"next":{"fields":{"title":"Last Day","date":"15 Jul 2005","slug":"/2005-07-15-last-day/"}},"previous":{"fields":{"title":"Wanna Ruin Your Resumé?","date":"15 Jul 2005","slug":"/2005-07-15-wanna-ruin-your-resum/"}}},{"node":{"fields":{"title":"Last Day","date":"15 Jul 2005","slug":"/2005-07-15-last-day/","tags":"Y2005"},"html":"<p>Yesterday was Christopher's last kindergarten day for the year. It was a bit sad since the two leaders of his clique won't be coming back, and it was also the last day for his favorite <i>Zivi</i> (<a href=\"http://www.dict.cc/?s=Zivildienstleistender\" title=\"conscientious objector performing community service\"><i>Zivildienstleistender</i></a>). Today was the end-of-year fest, and now he has 3-1/2 weeks off. Next year his group will have 8 instead of 9 kids, and the funding for kindergarten will be reduced proportionally (thanks to Christian Wulff's social budget cuts in Niedersachsen... funny how he dropped out of the news after new elections were proposed). </p>\n<p>Now Christopher can look forward to flying to see Grandma and Grandpa, and going fishing in Grandpa's boat. Mama and Papa are looking forward to that too.</p>"},"next":{"fields":{"title":"What do the conservatives want?","date":"11 Jul 2005","slug":"/2005-07-11-what-do-the-conservatives-want/"}},"previous":{"fields":{"title":"Not That Starbuck","date":"15 Jul 2005","slug":"/2005-07-15-not-that-starbuck/"}}},{"node":{"fields":{"title":"What do the conservatives want?","date":"11 Jul 2005","slug":"/2005-07-11-what-do-the-conservatives-want/","tags":"Y2005"},"html":"<p>Sign and Sight: <a href=\"http://www.signandsight.com/features/247.html\" title=\"What do the conservatives want? - signandsight\">What do the conservatives want? A look at the tradition of German conservatism.</a> Promising \"blooming landscapes\" rather than calling for \"blood, sweat and tears\".</p>"},"next":{"fields":{"title":"Berlin! Berlin!","date":"11 Jul 2005","slug":"/2005-07-11-berlin-berlin/"}},"previous":{"fields":{"title":"Last Day","date":"15 Jul 2005","slug":"/2005-07-15-last-day/"}}}],"pathPrefix":"","first":false,"last":false,"index":295,"pageCount":1101}}

